/**
 *  url  fragment hash  # 
 *
 * 
 * : http://example.com/path/?query=d#123 => 123
 *
 * @param {String} url url
 * @returns {String}
 */
export const getUrlFragment = url => {
    var hashIndex = url.indexOf('#');
    //return hashIndex === -1 ? '' : url.slice(hashIndex + 1);
    return hashIndex === -1 ? '/' : url.slice(hashIndex + 1);
}

/**
 * 
 *
 *  http://a.com/b/c
 * :
 * d => http://a.com/b/d
 * /e => http://a.com/e
 * #1 => http://a.com/b/c#1
 * http://b.com/f => http://b.com/f
 *
 * @param {String} url url
 * @returns {String}
 */
export const getAbsoluteUrl = url => {
    var link = document.createElement('a');
    link.setAttribute('href', url);
    var absoluteUrl = link.href;
    link = null;
    return absoluteUrl;
}

/**
 *  url  hash
 *
 * @param {String} url url
 * @returns {String}
 */
export const getBaseUrl = url => {
    var hashIndex = url.indexOf('#');
    return hashIndex === -1 ? url.slice(0) : url.slice(0, hashIndex);
}

/**
 *  url  base  fragment 
 *
 * @param {String} url url
 * @returns {UrlObject}
 */
export const toUrlObject = url => {
    var fullUrl = getAbsoluteUrl(url),
        baseUrl = getBaseUrl(fullUrl),
        fragment = getUrlFragment(url);

    return {
        base: baseUrl,
        full: fullUrl,
        original: url,
        fragment: fragment
    };
}

/**
 *  sessionStorage true false
 * @returns {Boolean}
 */
export const supportStorage = () => {
    var mod = 'ob.router.storage.ability';
    try {
        sessionStorage.setItem(mod, mod);
        sessionStorage.removeItem(mod);
        return true;
    } catch(e) {
        return false;
    }
}


