import ExposeActionPool from '../annotations/actions/pool';
import ImportAction from './importAction'

const Container = function(container) {
  return function(app, reinit) {
    return {
      actions: {
        container: {
          modify: (model, data, actions, error) => {
            // container.register
            if (typeof data === 'object') {
              for(var key in data) {
                container.register(key, {
                  useClass: data[key]
                });
              }

              // 
              var mutativeEffects = ImportAction(container)();
              // effect
              reinit(mutativeEffects.effects)

              // domdom
              actions.$updateModel(model)
            }
          },
        }
      }
    }
  }
}

export default Container



