export default function (options) {
    return {
        actions: {
            model: {
                /**
                 * model
                 * @param state {Any} model
                 */
                setState: (model, state) => state,

                /**
                 * model
                 * @param state {Object} model
                 */
                setObjectState: (model, state) => {
                    if (typeof state !== 'object') {
                        throw new Error('parameter of action.model.setObjectState must be object type.')
                    }
                    // 
                    let obj = {}
                    // statemodelobj
                    for (let key in state) {
                        obj[key] = {}
                        for (let protertyKey in model[key]) {
                            let value = state[key][protertyKey]
                            if (value) {
                                obj[key][protertyKey] = value
                            } else {
                                obj[key][protertyKey] = model[key][protertyKey]
                            }
                        }
                    }
                    // return $.extend(true, model, state)  // bug
                    return obj
                },
            }
        },
    }
}


