export default function (options) {

  // prompt
  let prompt = null

  return {
    model: {
      router: match(options.view, _getUrlFragment(location.href))
    },
    actions: {
      router: {
        match: function (_, data) {
          return {
            router: match(options.view, _getUrlFragment(data))
          }
        },
        go: function (_, data, actions) {
          // history.pushState({}, "", data)
          actions.router.match(data.indexOf('#') == 0 ? data : '#' + data)
        },
        back: function () {
          history.back()
        },
        // 
        block: function (_, data) {
            prompt = typeof data === 'function' ? data : null
        },
        // 
        destoryBlock: function () {
            prompt = null
        }
      }
    },
    hooks: {
      onRender: function (model) {
        $.loadDocument(model.router.match)
        return options.view[model.router.match]
      },
      onRendered: function (model, view, node) {
        // 
        if($.router)
            if(!$.router.popstateFlag){
                // hash
                $.router.load(model.router)
            }
            else {
                $.router.popstateFlag = false
            }
      }
    },
    readies: [
      function (_, actions) {
        addEventListener("popstate", function (e) {
            // 
            if (prompt){
                prompt()

                // 2017-12-20 blockbug
                prompt = null
            }else{
                // popState
                $.router.popstateFlag = true
                // statehistory
                var router = match(options.view, _getUrlFragment(location.href))
                // statehistory
                actions.router.match(location.href)
                // router.matchpageIdPage
                $.router._onPopState(e, router.match)
            }
        })
      }
    ]
  }
}

function match(routes, path) {
  var match, params = {}

  // hashgetbugQQ
  if (path.indexOf('&') != -1) {
      path = path.substr(0, path.indexOf('&'))
  }

  for (var route in routes) {
    var keys = []

    if (route === "*") {
      continue
    }

    path.replace(new RegExp("^" + route
      .replace(/\//g, "\\/")
      .replace(/:([A-Za-z0-9_]+)/g, function (_, key) {
        keys.push(key)
        return "([-A-Za-z0-9_]+)"
      }) + "/?$", "g"), function () {

        for (var i = 1; i < arguments.length - 2; i++) {
          params[keys.shift()] = arguments[i]
        }
        match = route
      })

    if (match) {
      break
    }
  }

  return {
    match: match || "*",
    params: params,
    path: path
  }
}

function _getUrlFragment(url) {
  var hashIndex = url.indexOf('#');
  return hashIndex === -1 ? '/' : url.slice(hashIndex + 1);
}


