import ExposeActionPool from '../annotations/actions/pool';

const ImportAction = function(container) {
  return function() {
    let effects = {};
    const $pool = ExposeActionPool._pool
    if ($pool.size > 0) {
      $pool.forEach(function(value, key, map){
        const ctor = key.constructor
        if (ctor) {
          const valueLength = value.length
          if (valueLength > 0) {
            // 
            const instance = container.resolve(key.constructor) // 
            for(var i = 0, j = valueLength; i < j; i++) {
              effects[value[i]] = instance[value[i]]
            }
          }
        }
      })
    }
    return {
      effects
    }
  }
}

export default ImportAction



