/**
 * .0
 * @param num 
 * @param n   
 * @returns {*}
 */
constpadZero = (num,n) =>{
    letlen=num.split('.').length;
    while(len<n){
        num=num + '.0';
        len++;
    }
    returnnum;
}

/**
 * APP
 * @param _
 * @returns {*}
 */
const getVersion = _ => {
    let groups = navigator.userAgent.toLowerCase().match(/DFYJ\/([\d.]+)/i)
    if (!groups) {
        return undefined
    }else {
        return groups[1]
    }
}

/**
 * arg1 > arg2, return 1; arg1 == arg2, return 0; arg1 < arg2, return -1
 * @param compareVersion
 * @returns {number}
 */
const _compareVersion = (a, b) => {
    // .0
    var _as = a.split('.');
    var _bs = b.split('.');
    var max = Math.max(_as.length, _bs.length)
    a = padZero(a, max)
    b = padZero(b, max)

    var as = a.split('.');
    var bs = b.split('.');
    if (a === b) return 0;

    for (var i = 0; i < as.length; i++) {
        var x = parseInt(as[i]);
        if (!bs[i]) return 1;
        var y = parseInt(bs[i]);
        if (x < y) return -1;
        if (x > y) return 1;
    }
    return -1;
}

/**
 * APPcompareVersion1-10
 * @param compareVersion
 * @returns {number}
 */
const compareVersion = (compareVersion) => {
    // 
    let currentVersion = getVersion()
    if (!currentVersion) {
        return 1
    } else {
        return _compareVersion(currentVersion, compareVersion)
    }
}

export {
    getVersion,
    compareVersion,
    _compareVersion,
}



